import React, { useEffect, useRef } from 'react';
import PropTypes from 'prop-types';
import sr from '@utils/sr';
import { srConfig, email } from '@config';
import styled from 'styled-components';
import { theme, mixins, media, Section, Heading } from '@styles';
import { IconLinkedin, IconInstagram, IconPinterest, IconTwitter } from '@components/icons';

const { colors, fontSizes, fonts } = theme;

const StyledContainer = styled(Section)`
  text-align: center;
  max-width: 600px;
  margin: 0 auto 100px;
  a {
    ${mixins.inlineLink};
  }
`;
const StyledHeading = styled(Heading)`
  display: block;
  color: ${colors.green};
  font-size: ${fontSizes.md};
  font-family: ${fonts.SFMono};
  font-weight: normal;
  margin-bottom: 20px;
  justify-content: center;
  ${media.desktop`font-size: ${fontSizes.sm};`};
  &:before {
    bottom: 0;
    font-size: ${fontSizes.sm};
    ${media.desktop`font-size: ${fontSizes.smish};`};
  }
  &:after {
    display: none;
  }
`;
const StyledTitle = styled.h4`
  margin: 0 0 20px;
  font-size: 60px;
  ${media.desktop`font-size: 50px;`};
  ${media.tablet`font-size: 40px;`};
`;
const StyledEmailLink = styled.a`
  ${mixins.bigButton};
  margin-top: 50px;
`;
const ResumeLink = styled.a`
  ${mixins.bigButton};
  padding: 18px 50px;
  margin-left: 20px;
  width: max-content;
`;
const StyledSocialLinks = styled.div`
  display: flex;
  justify-content: center;
  margin-top: 30px;

  a {
    margin: 0 10px;
    svg {
      width: 30px;
      height: 30px;
      fill: ${colors.green};
      &:hover,
      &:focus {
        fill: ${colors.lightGreen};
      }
    }
  }
`;

const StyledFollowMe = styled.p`
  font-size: ${fontSizes.sm};
  color: ${colors.lightSlate};
  margin-top: 20px;
`;

const Contact = ({ data }) => {
  const { frontmatter, html } = data[0].node;
  const { title, buttonText } = frontmatter;
  const revealContainer = useRef(null);
  useEffect(() => sr.reveal(revealContainer.current, srConfig()), []);

  return (
    <StyledContainer id="contact" ref={revealContainer}>
      <StyledHeading>What&apos;s Next?</StyledHeading>
      <StyledTitle>{title}</StyledTitle>
      <div dangerouslySetInnerHTML={{ __html: html }} />
      <StyledEmailLink href={`mailto:${email}`} target="_blank" rel="nofollow noopener noreferrer">
        {buttonText}
      </StyledEmailLink>
      <ResumeLink href="/resume.pdf" target="_blank" rel="nofollow noopener noreferrer">
        Resume
      </ResumeLink>
      <StyledFollowMe>
        Join my network and stay connected to stay connected with me:
        <StyledSocialLinks>
          <a href="https://www.instagram.com/khilan.sk" target="_blank" rel="noopener noreferrer">
            <IconInstagram />
          </a>
          <a
            href="https://www.linkedin.com/in/khilan005/"
            target="_blank"
            rel="noopener noreferrer">
            <IconLinkedin />
          </a>
          <a href="http://www.proz.com/profile/3562571" target="_blank" rel="noopener noreferrer">
            <IconPinterest />
          </a>
          <a href="https://twitter.com/khilan005" target="_blank" rel="noopener noreferrer">
            <IconTwitter />
          </a>
        </StyledSocialLinks>
      </StyledFollowMe>
    </StyledContainer>
  );
};

Contact.propTypes = {
  data: PropTypes.array.isRequired,
};

export default Contact;
